import React, { useCallback, useState } from 'react'
import { saveAs } from 'file-saver'

import Button from '../components/Button'

import svgToCanvas from '../utils/svgToCanvas'
import '../vendor/Blob'
import '../vendor/canvas-toBlob'

const DownloadChart = ({ chartRef, width, height, name }) => {
  const [downloading, setDownloading] = useState()
  const handleDownload = useCallback(async () => {
    setDownloading(true)
    const date = new Date().toLocaleDateString()
    const serializer = new XMLSerializer();
    let source = serializer.serializeToString(chartRef.current);
    source = source.replace(/^<svg/, `<svg width="${width}" height="${height}"`);
    // if (!source.match(/^<svg[^>]+xmlns="http:\/\/www\.w3\.org\/2000\/svg"/)) {
    //   source = source.replace(/^<svg/, '<svg xmlns="http://www.w3.org/2000/svg"');
    // }
    // if (!source.match(/^<svg[^>]+"http:\/\/www\.w3\.org\/1999\/xlink"/)) {
    //   source = source.replace(/^<svg/, '<svg xmlns:xlink="http://www.w3.org/1999/xlink"');
    // }
    const canvas = await svgToCanvas(source, width, height)
    canvas.toBlob((blob) => {
      saveAs(blob, `${name}-${date}.png`);
      setDownloading(false)
    });

    //convert svg source to URI data scheme.
    // const blob = new Blob([source], { type: 'image/svg+xml' });
    // saveAs(blob, `${date}.svg`);
  }, [chartRef, height, width, name])
  return <Button isLoading={downloading} onClick={handleDownload}>下載PNG</Button>
}

export default DownloadChart
