import React from 'react'
import { Heading, HStack } from "@chakra-ui/react";
import { Link as RouterLink } from 'react-router-dom'

import Link from '../components/Link';
import Flex from '../components/Flex';

const links = [
  { label: '地圖', path: '/' },
  { label: '火柴棒圖', path: '/scatter' },
]

const Header = ({ siteTitle, ...props }) => (
  <Flex
    position="fixed"
    bg="orange.400"
    top={0}
    left={0}
    right={0}
    alignItems="center"
    zIndex="docked"
    {...props}
  >
    <HStack px="1em" spacing="2em">
      <Heading as="h1" color="white">
        {siteTitle}
      </Heading>
      <HStack spacing="1em">
        {links.map(({ label, path }, i) => <Link key={i} fontSize="1.25em" color="white" to={path} as={RouterLink}>{label}</Link>)}
      </HStack>
    </HStack>
  </Flex>
)

export default Header
