import { tsv } from "d3-fetch"
import { useEffect, useState } from "react"

const useTsv = (path) => {
  const [loading, setLoading] = useState()
  const [error, setError] = useState()
  const [data, setData] = useState()
  useEffect(() => {
    setLoading(true)
    setError(null)
    tsv(path).then(d => {
      setLoading(false)
      setData(d)
    }).catch(e => {
      setLoading(false)
      setError(e)
    })
  }, [path])
  return { data, loading, error }
}

export default useTsv
