import { Heading, Stack } from '@chakra-ui/layout'
import React, { createElement, useMemo } from 'react'
import { useLocalStorage } from 'react-use'
import { tsvParse } from 'd3-dsv'

import DataManager from './DataManager'

const requiredFields = {
  map: {
    name: '國家名',
    value: '數值',
  },
  scatter: {
    x1: '前期出口額',
    x2: '當期出口額',
    y1: '前期年增率',
    y2: '當期年增率',
    group: '產業分類',
    name: '產業細項',
  },
}

const ChartEditor = ({ type, chart, title }) => {
  const [config, setConfig] = useLocalStorage(`re-trading-config:${type}`, {})
  const [pastedData, setPastedData] = useLocalStorage(`re-trading-data:${type}`, '')
  const data = useMemo(() => tsvParse(pastedData).map((d, i) => ({ ...d, id: i + 1 })), [pastedData])
  // console.log(data)
  // console.log(config)
  return (
    <Stack p="1em">
      <Heading>製作{title}</Heading>
      <DataManager
        value={pastedData}
        config={config}
        data={data}
        fields={requiredFields[type]}
        onConfigChange={setConfig}
        onValueChange={setPastedData}
      />
      {data && data.length && createElement(chart, { data, config })}
    </Stack>
  )
}

export default ChartEditor
