import React, { useMemo, useRef } from 'react'
import { scaleLinear, scaleLog } from '@visx/scale'
import { groupBy, mapValues } from 'lodash'
import { AspectRatio, Stack } from "@chakra-ui/react"
import { useFormik } from 'formik'

import ScatterLink from '../../components/Charts/ScatterLink'
import Box from '../../components/Box'
import HightlightCheckbox from './HightlightCheckbox'
import DownloadChart from '../DownloadChart'

const w = 814
const h = 380

const GrowthScatter = ({ data, config= {} }) => {
  const svgRef = useRef()
  const groups = useMemo(() => groupBy(data, config.group), [data, config.group])
  const formik = useFormik({ initialValues: {} })
  const filteredData = useMemo(() => {
    if (data) {
      return data && data.filter(d => d[config.x1] > 0 && d[config.x2] > 0)
    }
    return data
  }, [data, config])
  const highlight = useMemo(() => mapValues(formik.values, v => v && v.reduce((a, k) => {
    a[k] = true
    return a
  }, {})), [formik.values])

  const requiredDone = useMemo(() => ['x1', 'x2', 'y1', 'y2'].every(k => config[k]), [config])
  return (
    <Stack spacing="1em" py="1em">
      <HightlightCheckbox
        options={groups}
        formik={formik}
        nameKey={config.name}
      />
      {requiredDone && (
        <>
          <Box p="2em" bg="gray.100" rounded="2xl">
            <AspectRatio ratio={w / h}>
              <ScatterLink
                width={w}
                height={h}
                data={filteredData}
                xScale={scaleLog}
                yScale={scaleLinear}
                series={[
                  { x: d => d[config.x1] * 1, y: d => d[config.y1] * 1, label: config.x1 },
                  { x: d => d[config.x2] * 1, y: d => d[config.y2] * 1, label: config.x2 },
                ]}
                highlight={highlight}
                ref={svgRef}
              />
            </AspectRatio>
          </Box>
          <Box>
            <DownloadChart chartRef={svgRef} width={w} height={h} name="火柴棒圖" />
          </Box>
        </>
      )}
    </Stack>
  )
}

export default GrowthScatter
