const PIXEL_RATIO = (function () {
  if (typeof window === 'undefined') return 1
  const ctx = document.createElement('canvas').getContext('2d'),
      dpr = window.devicePixelRatio || 1,
      bsr = ctx.webkitBackingStorePixelRatio ||
            ctx.mozBackingStorePixelRatio ||
            ctx.msBackingStorePixelRatio ||
            ctx.oBackingStorePixelRatio ||
            ctx.backingStorePixelRatio || 1;

  return dpr / bsr;
})();

const setHiDPICanvas = function(canvas, w, h, ratio) {
  if (!ratio) { ratio = PIXEL_RATIO; }
  const can = canvas;
  can.width = w * ratio;
  can.height = h * ratio;
  can.style.width = w + 'px';
  can.style.height = h + 'px';
  can.getContext('2d').setTransform(ratio, 0, 0, ratio, 0, 0);
}

const svgToCanvas = (svgStr, width, height) => {
  if (!svgStr || !width || !height) return Promise.resolve(null)
  const canvas = document.createElement('canvas');
  setHiDPICanvas(canvas, width, height)
  const ctx = canvas.getContext('2d');
  const url = 'data:image/svg+xml; charset=utf8, ' + encodeURIComponent(svgStr);
  const image = new Image();

  return new Promise((res) => {
    image.onload = () => {
      ctx.drawImage(image, 0, 0);
      URL.revokeObjectURL(url);
      res(canvas)
    }
    image.src = url;
  })
}

export default svgToCanvas
