import { IconButton } from '@chakra-ui/button';
import { Box, HStack } from '@chakra-ui/layout';
import { Textarea } from '@chakra-ui/textarea';
import React, { useCallback, useEffect, useMemo, useRef, useState } from 'react'
import { MdClose, MdEdit, MdRefresh } from 'react-icons/md'
import { Button } from '@chakra-ui/button';
import Table from 'rc-table';
import styled from '@emotion/styled';
import { FormControl, FormLabel } from '@chakra-ui/form-control';
import { Select } from '@chakra-ui/select';
import { useFormik } from 'formik';
import { differenceBy } from 'lodash';

const StyledTable = styled(Table)`
table {
  width: 100%;
}
td {
  padding: 0.25em;
  border: 1px solid lightGray;
}
.rc-table-body {
  overflow-x: hidden;
}
`

const swapObj = obj => {
  const values = Object.entries(obj).filter(v => v[1]).map(v => [v[1], v[0]])
  return Object.fromEntries(values)
}

const DataManager = ({ onValueChange, onConfigChange, config, value, data, fields }) => {
  const ref = useRef()
  const formik = useFormik({ initialValues: swapObj(config) })
  const [isEditing, setIsEditing] = useState(!value)
  const handleClick = useCallback(() => {
    onValueChange(ref.current.value)
    setIsEditing(false)
  }, [onValueChange])
  useEffect(() => {
    onConfigChange(swapObj(formik.values))
  }, [formik.values, onConfigChange])
  const availFields = useMemo(() => differenceBy(Object.entries(fields), Object.values(formik.values).map(v => [v]), 0), [formik.values, fields])
  const columns = useMemo(() => data[0] ? Object.keys(data[0]).filter(key => key !== 'id').map(key => ({
    key,
    title: (
      <HStack alignItems="flex-end" p="0.25em">
        <FormControl id={key}>
          <FormLabel>{key}</FormLabel>
          {formik.values[key] ? fields[formik.values[key]] : (
            <Select
              placeholder="選擇欄位類型"
              onChange={formik.handleChange}
              value={formik.values[key]}
            >
              {availFields.map(([val, label], i) => <option key={i} value={val}>{label}</option>)}
            </Select>
          )}

        </FormControl>
        {formik.values[key] && <IconButton icon={<MdClose />} onClick={() => formik.setFieldValue(key, '')} />}
      </HStack>
    ),
    dataIndex: key,
  })) : [], [data, availFields, fields, formik])
  return (
    <Box>
      {isEditing ? (
        <>
          <Textarea placeholder="直接貼上excel資料" defaultValue={value} ref={ref} />
          <Button onClick={handleClick} mt="1em">儲存</Button>
        </>
      ) : (
        <Box>
          <HStack justifyContent="flex-end" spacing="1em" mb="0.5em">
            <Button variant="outline" onClick={() => formik.setValues({})} leftIcon={<MdRefresh />}>重設欄位</Button>
            <Button variant="outline" onClick={() => setIsEditing(true)} leftIcon={<MdEdit />}>編輯資料</Button>
          </HStack>
          <StyledTable
            columns={columns}
            data={data}
            scroll={{ y: 300 }}
            rowKey="id"
            useFixedHeader
          />
        </Box>
      )}
    </Box>
  )
}

export default DataManager
