import { useToken } from '@chakra-ui/system'
import React, { forwardRef, useCallback, useMemo } from 'react'
import { Mercator } from '@visx/geo';
import * as topojson from 'topojson-client';
import { BarRounded } from '@visx/shape';
import { scaleQuantize } from '@visx/scale';
import { Group } from '@visx/group';
import { range } from 'lodash';
import { format } from 'd3-format';

import topology from './world-topo.json';

import theme from '../ThemeProvider/theme'
import arrToObj from '../../utils/arrToObj'
import getExtent from './getExtent';

const world = topojson.feature(topology, topology.objects.countries)
const commaFormat = format(',')

const Choropleth = forwardRef(({
  width,
  height,
  data,
}, ref) => {
  // console.log(data)
  const colors = useToken('colors', ['chart.blue', 'chart.yellow', 'chart.orange', 'chart.red'])
  const centerX = width / 2;
  const centerY = height / 2;
  const scale = (width / 1100) * 100;
  const xMax = useMemo(() => {
    const [, max] = getExtent(data, 'value')
    return Math.ceil(Math.log10(max))
  }, [data])

  const colorScale = useCallback((v) => scaleQuantize({
    domain: [0, xMax],
    range: colors,
  })(Math.log10(v)), [xMax, colors]);

  const keyedData = useMemo(() => arrToObj(data, 'id'), [data])

  const legend = useMemo(() => range(0, 9, 2).reduce((all, p, i) => all.concat([
    i ? <rect key={`bar-${i}`} x={50 * (i - 1)} y={4} width={50} height={4} fill={colorScale(10 ** (p - 2))} /> : null,
    (<text
      fontSize={7}
      fontWeight="700"
      x={50 * i}
      textAnchor="middle"
      key={p}
    >{p ? commaFormat(10 ** p) : 0}</text>)
  ]), []), [colorScale])

  const renderMap = mercator => (
    <g>
      {mercator.features.map(({ feature, path }, i) => (
        <path
          key={`map-feature-${i}`}
          d={path || ''}
          fill={colorScale(keyedData[feature.properties.name]?.value || 0)}
        />
      ))}
    </g>
  )

  return (
    <svg
      viewBox={`0 0 ${width} ${height}`}
      ref={ref}
      style={{ width: '100%', fontFamily: theme.fonts.body }}
    >
      <BarRounded height={height} width={width} fill="#D2D2D2" radius={15} x={0} y={0} all />
      <BarRounded height={height - 5} width={width} fill="white" radius={15} x={0} y={0} all />
      <g
        style={{
          transformOrigin: `${centerX}px ${centerY}px`,
          transform: 'scale(1.3, 1)',
        }}
      >
        <Mercator
          data={world.features}
          scale={scale}
          center={[180, 45]}
          translate={[centerX, centerY]}
          clipExtent={[[width * 0.191, 0], [width / 2, height]]}
        >
          {renderMap}
        </Mercator>
        <Mercator
          data={world.features}
          scale={scale}
          center={[-180, 45]}
          translate={[centerX, centerY]}
          clipExtent={[[width / 2, 0], [width * 0.761, height]]}
        >
          {renderMap}
        </Mercator>
      </g>
      <Group left={16} top={height - 20}>
        {legend}
      </Group>
    </svg>
  )
})

export default Choropleth
