import { AspectRatio, Box, Flex, Stack, Text } from '@chakra-ui/layout'
import { Tag } from '@chakra-ui/tag'
import React, { useMemo, useRef } from 'react'

import Choropleth from '../../components/Charts/Choropleth'
import useTsv from '../../utils/useTsv'
import DownloadChart from '../DownloadChart'

import country from './countries.tsv'

const w = 576
const h = 232

const CountryChoro = ({ data, config= {} }) => {
  const svgRef = useRef()
  const { data: countries, loading } = useTsv(country)
  const requiredDone = useMemo(() => ['name', 'value'].every(k => config[k]) && !loading, [config, loading])
  const [namedData, missedData] = useMemo(() => {
    const defaultValue = [[], []]
    if (!countries) return defaultValue
    const countryEn = countries.reduce((en, c) => {
      en[c.name] = c.eng
      return en
    }, {})
    return data.reduce((all, d) => {
      const name = d[config.name]
      const value = d[config.value] * 1
      const id = countryEn[name]
      if (id) {
        all[0].push({ id, name, value })
      } else {
        all[1].push({ name, value })
      }
      return all
    }, defaultValue)
  }, [countries, data, config])
  return (
    <Stack>
      {requiredDone && (
        <>
          {missedData.length && (
            <Box>
              <Text>對應不到英文的國家：</Text>
              <Flex flexWrap="wrap">{missedData.map((d, i) => <Tag m="0.5em" key={i}>{d.name} ({d.value})</Tag>)}</Flex>
            </Box>
          )}
          <Box p="2em" bg="gray.100" rounded="2xl">
            <AspectRatio ratio={w / h}>
              <Choropleth
                width={w}
                height={h}
                data={namedData}
                ref={svgRef}
              />
            </AspectRatio>
          </Box>
          <Box>
            <DownloadChart chartRef={svgRef} width={w} height={h} name="地圖" />
          </Box>
        </>
      )}
    </Stack>
  )
}

export default CountryChoro
