import { AxisLeft, AxisBottom } from '@visx/axis';
import { GridColumns, GridRows } from '@visx/grid';
import { Group } from '@visx/group'
import { BarRounded, Circle, Line } from '@visx/shape'
import { isObject, maxBy, minBy, range } from 'lodash';
import React, { forwardRef, useMemo } from 'react'
import { format } from 'd3-format'
import { useTooltip, useTooltipInPortal, defaultStyles } from '@visx/tooltip';
import { localPoint } from '@visx/event';
import { HStack, Stack } from '@chakra-ui/layout';
import composeRefs from '@seznam/compose-react-refs'
import { useToken } from '@chakra-ui/system';
import { useMeasure } from 'react-use';

import theme from '../ThemeProvider/theme'

import Text from '../Text'
import getExtent from './getExtent';

const defaultMargin = {
  top: 36,
  right: 52,
  bottom: 45,
  left: 60,
};

const tooltipStyles = {
  ...defaultStyles,
  padding: 12,
  minWidth: 60,
  backgroundColor: 'rgba(0,0,0,0.9)',
  color: 'white',
};

const perFormat = format('.2r')
const commaFormat = format(',')
const commaSignedFormat = format('+,')
let tooltipTimeout

const Legend = ({ x, y, fill, children }) => (
  <Group left={x} top={y}>
    <rect width={24} height={10} fill={fill} x={-32} y={0} />
    <text x={0} y={10} fontWeight="900" fontSize="13">{children}</text>
  </Group>
)

const ScatterLink = forwardRef(({
  width,
  height,
  margin = defaultMargin,
  xScale: xScaleBase,
  yScale: yScaleBase,
  data,
  series,
  highlight,
}, ref) => {
  const {
    tooltipOpen,
    tooltipLeft,
    tooltipTop,
    tooltipData,
    hideTooltip,
    showTooltip,
  } = useTooltip();
  const [measureRef, canvasSize] = useMeasure()
  const [chartRed, chartGreen] = useToken('colors', ['chart.red', 'chart.green'])
  const { containerRef, TooltipInPortal } = useTooltipInPortal({
    // TooltipInPortal is rendered in a separate child of <body /> and positioned
    // with page coordinates which should be updated on scroll. consider using
    // Tooltip or TooltipWithBounds if you don't need to render inside a Portal
    scroll: true,
  });

  const allData = series.reduce((all, { x, y }) => all.concat(data.map(d => ({ x: x(d), y: y(d) }))), [])
  // console.log(allData)
  // bounds
  const xMax = width - margin.left - margin.right;
  const yMax = height - margin.top - margin.bottom;
  const xScale = useMemo(() => xScaleBase && xScaleBase({
    domain: getExtent(allData, 'x').map((d, i) => i ? d * 1.1 : d * 0.9), // x-coordinate data values
    range: [0, xMax], // svg x-coordinates, svg x-coordinates increase left to right
  }), [allData, xMax, xScaleBase]);
  const xTickValues = useMemo(() => {
    if (!xScale) return []
    const domain = xScale.domain()
    const min = Math.log10(domain[0])
    const max = Math.log10(domain[1])
    return range(Math.ceil(min), max).map(x => 10 ** x)
  }, [xScale])
  const yScale = useMemo(() => yScaleBase && yScaleBase({
    domain: getExtent(allData, 'y'), // y-coordinate data values
    range: [yMax, 0], // svg y-coordinates, svg y-coordinates increase left to right
  }), [allData, yMax, yScaleBase]);
  if (width < 10 || !data || !xScaleBase || !yScaleBase || !series) return null;
  return (
    <div ref={measureRef}>
      <svg
        viewBox={`0 0 ${width} ${height}`}
        ref={composeRefs(ref, containerRef)}
        style={{ width: '100%', fontFamily: theme.fonts.body }}
      >
        <BarRounded height={height} width={width} fill="white" radius={30} x={0} y={0} all />
        <Group left={margin.left} top={margin.top}>
          <GridRows
            scale={yScale}
            width={xMax}
            height={yMax}
            stroke="black"
            strokeOpacity={0.1}
            strokeDasharray="3 3"
          />
          <GridColumns
            scale={xScale}
            width={xMax}
            height={yMax}
            stroke="black"
            strokeOpacity={0.1}
            tickValues={xTickValues}
          />
          <AxisBottom
            top={yScale(0)}
            scale={xScale}
            strokeWidth={2}
            tickValues={xTickValues}
            tickFormat={commaFormat}
            tickLabelProps={() => ({ fontSize: 10, textAnchor: 'middle', fontWeight: 'bold' })}
          />
          <AxisLeft
            scale={yScale}
            hideAxisLine
            hideTicks
          />
          <Group>
            {data.map((d, j) => {
              const x1 = series[0].x(d)
              const x2 = series[1].x(d)
              const y1 = series[0].y(d)
              const y2 = series[1].y(d)
              const isHighlight = isObject(highlight[d.industry]) ? highlight[d.industry][d.item] : highlight[d.industry]
              return (
                <Line
                  key={`line-${j}`}
                  className="line"
                  from={{
                    x: xScale(x1),
                    y: yScale(y1),
                  }}
                  to={{
                    x: xScale(x2),
                    y: yScale(y2),
                  }}
                  stroke={x2 < x1 ? chartGreen : chartRed}
                  strokeWidth={3}
                  opacity={tooltipOpen && d.id !== tooltipData.id ? (isHighlight ? 0.3 : 0.05) : (isHighlight ? 0.9 : 0.1)}
                  onMouseLeave={() => {
                    tooltipTimeout = window.setTimeout(() => {
                      hideTooltip();
                    }, 300);
                  }}
                  onMouseMove={event => {
                    if (tooltipTimeout) clearTimeout(tooltipTimeout);
                    // TooltipInPortal expects coordinates to be relative to containerRef
                    // localPoint returns coordinates relative to the nearest SVG, which
                    // is what containerRef is set to in this example.
                    const eventSvgCoords = localPoint(event);
                    showTooltip({
                      tooltipData: { ...d, diff: x2 - x1 },
                      tooltipTop: eventSvgCoords?.y,
                      tooltipLeft: eventSvgCoords?.x,
                    });
                  }}
                />
              )
            })}
          </Group>
          <Group>
            {data.map((d, j) => {
              const isHighlight = isObject(highlight[d.industry]) ? highlight[d.industry][d.item] : highlight[d.industry]
              return (
                <Circle
                  key={`point-${j}`}
                  className="dot"
                  cx={xScale(series[1].x(d))}
                  cy={yScale(series[1].y(d))}
                  fill="black"
                  opacity={isHighlight ? 1 : (tooltipOpen && d.id !== tooltipData.id ? 0.05 : 0.3)}
                  r={3}
                />
              )
            })}
          </Group>
        </Group>
        <Group left={margin.left - 8} top={margin.top - 14}>
          <text
            fontSize={12}
            fontWeight="700"
            textAnchor="end"
          >年增率</text>
          <text
            fontSize={10}
            x={5}
            y={14}
            textAnchor="end"
          >（％）</text>
        </Group>
        <Group left={width - 28} top={yScale(0) + margin.top + 4}>
          <text
            fontSize={12}
            fontWeight="700"
            textAnchor="middle"
          >出口額</text>
          <text
            y={14}
            fontSize={10}
            textAnchor="middle"
          >(萬美元)</text>
        </Group>
        <Group left={width - 28} top={height - 26}>
          <Legend x={-260} fill={chartRed}>成長</Legend>
          <Legend x={-180} fill={chartGreen}>衰退</Legend>
          <Group left={-80}>
            {series.map((s, i) => <text fontSize={12} fontWeight="700" y={10} key={i} x={-52 + (i * 90)}>{s.label}</text>)}
            <rect width={30} height={7} fill="#DCDCDC" x={0} y={2} />
            <circle r={7} fill="black" strokeWidth={2} stroke="white" cx={26} cy={5} />
          </Group>
        </Group>
        {tooltipOpen && tooltipData && (
          <TooltipInPortal top={tooltipTop * canvasSize.height / height} left={tooltipLeft * canvasSize.width / width} style={tooltipStyles}>
            <Stack>
              <Text fontWeight="bold" fontSize="0.875em">
                {tooltipData.industry}
              </Text>
              <Text fontSize="1.125em">
                {tooltipData.item}
              </Text>
              <HStack py="0.5em" spacing="1em">
                {series.map(({ x, y, label }, i)=> (
                  <Stack key={i}>
                    <Text>{label}:</Text>
                    <Text fontWeight="bold" fontSize="1.125em">{commaFormat(x(tooltipData))}</Text>
                    <Text>{perFormat(y(tooltipData))}%</Text>
                  </Stack>
                ))}
              </HStack>
              <Text
                fontWeight="bold"
                fontSize="1.125em"
                color={tooltipData.diff < 0 ? 'chart.green' : 'chart.red'}
              >
                {commaSignedFormat(tooltipData.diff)}
              </Text>
            </Stack>
          </TooltipInPortal>
        )}
      </svg>
    </div>
  )
})

export default ScatterLink
