import React from 'react';
import { Switch, Route } from 'react-router-dom'
import ChartEditor from './containers/ChartEditor';
import GrowthScatter from './containers/GrowthScatter';
import CountryChoro from './containers/CountryChoro';

import Layout from './containers/Layout';

function App() {
  return (
    <Layout>
      <Switch>
        <Route path="/scatter">
          <ChartEditor key="scatter" type="scatter" chart={GrowthScatter} title="火柴棒圖" />
        </Route>
        <Route path="/">
          <ChartEditor key="map" type="map" chart={CountryChoro} title="地圖" />
        </Route>
      </Switch>
    </Layout>
  );
}

export default App;
