import { Checkbox, CheckboxGroup } from '@chakra-ui/checkbox'
import { Box, Flex, Heading, HStack, Stack, Text } from '@chakra-ui/layout'
import { indexOf, map } from 'lodash'
import React, { useMemo } from 'react'

const OptionGroups = ({ name, value = [], onChange, options = [] }) => {
  const checkedItems = useMemo(() => options.map(v => indexOf(value, v) > -1), [options, value])
  const allChecked = checkedItems.every(Boolean)
  const isIndeterminate = checkedItems.some(Boolean) && !allChecked
  return (
    <Stack>
      <HStack>
        <Heading as="h3" fontSize="md">{name}</Heading>
        {options.length > 1 && (
          <Box fontWeight="bold">
            <Checkbox
              colorScheme="orange"
              isChecked={allChecked}
              isIndeterminate={isIndeterminate}
              onChange={(e) => onChange(e.target.checked ? options : [])}
            >
              全部
            </Checkbox>
          </Box>
        )}
      </HStack>
      <CheckboxGroup
        colorScheme="orange"
        id={name}
        onChange={onChange}
        value={value}
      >
        <Flex wrap="wrap">
          {options.map((n, j) => typeof n === 'string' && (
            <Box key={n + j} m="0.25em">
              <Checkbox value={n}>{n}</Checkbox>
            </Box>
          ))}
        </Flex>
      </CheckboxGroup>
    </Stack>
  )
}

const HightlightCheckbox = ({ options, formik, nameKey }) => {
  const groups = useMemo(() => map(options, (children, name) => ({ name, children: map(children, nameKey) })).sort((a, b) => a.children.length - b.children.length), [options, nameKey])
  if (!nameKey) return null
  return (
    <Box>
      <Text fontSize="lg" fontWeight="bold">強調品項</Text>
      <Flex flexWrap="wrap" mx="-0.5em">
        {groups.map((g, i) => (
          <Box key={g.name + i} p="0.5em" m="0.5em" border="1px solid" borderColor="gray.300">
            <OptionGroups
              name={g.name}
              onChange={checked => formik.setFieldValue(g.name, checked)}
              value={formik.values[g.name]}
              options={g.children}
            />
          </Box>
        ))}
      </Flex>
      {/* <Tabs colorScheme="orange">
        <TabList flexWrap="wrap">
          {groups.map((g, i) => (
            <Tab key={g.name + i} color={formik.values[g.name]?.length ? 'orange.600' : 'inherit'}>
              {g.name}
              <Text ml="0.5em" fontSize="xs">({formik.values[g.name]?.length || 0}/{g.children.length})</Text>
            </Tab>
          ))}
        </TabList>
        <TabPanels>
          {groups.map((g, i) => (
            <TabPanel key={g.name + i}>
              <OptionGroups
                name={g.name}
                onChange={checked => formik.setFieldValue(g.name, checked)}
                value={formik.values[g.name]}
                options={g.children}
              />
            </TabPanel>
          ))}
        </TabPanels>
      </Tabs> */}
    </Box>
  )
}

export default HightlightCheckbox
